import {
  Button,
  Col,
  DatePicker,
  InputNumber,
  Row,
  Space,
  Typography,
} from "antd";
import React, { useState } from "react";
import dayjs, { Dayjs } from "dayjs";

export const SummCmp = () => {
  const [date, setDate] = useState<Dayjs | null>(null);
  const [time, setTime] = useState<Dayjs | null>(null);

  const [day, setDay] = useState<number | null>(null);
  const [hour, setHour] = useState<number | null>(null);
  const [minute, setMinute] = useState<number | null>(null);

  const [calcValue, setCalcValue] = useState<string | undefined>();

  const selectedDate =
    date &&
    time &&
    dayjs(`${date?.format("DD-MM-YYYY")} ${time?.format("HH:mm")}`);

  const onCalc = () => {
    const res = selectedDate
      ?.add(day ?? 0, "day")
      .add(hour ?? 0, "hour")
      .add(minute ?? 0, "minute")
      .format("DD-MM-YYYY HH:mm");
    setCalcValue(res);
  };

  const onReset = () => {
    setDate(null);
    setTime(null);
    setDay(null);
    setHour(null);
    setMinute(null);
    setCalcValue(undefined);
  };

  return (
    <Row gutter={[16, 16]}>
      <Col span={24}>
        <Space size={16}>
          <Typography.Text> </Typography.Text>
          <DatePicker
            value={date}
            onChange={setDate}
            format={"DD-MM-YYYY"}
            changeOnBlur
          />
        </Space>
      </Col>
      <Col span={24}>
        <Space size={16}>
          <Typography.Text> </Typography.Text>
          <DatePicker
            changeOnBlur
            value={time}
            onChange={setTime}
            picker="time"
            format={"HH:mm"}
          />
        </Space>
      </Col>

      <Col span={24}>
        <Space size={16}>
          <Typography.Text> :</Typography.Text>
          {selectedDate ? (
            <Typography.Text>
              {selectedDate.format("DD-MM-YYYY HH:mm")}
            </Typography.Text>
          ) : (
            ""
          )}
        </Space>
      </Col>

      <Col span={24}>
        <Space size={8}>
          <Space size={4}>
            <Typography.Text>:</Typography.Text>
            <InputNumber value={day} onChange={setDay} placeholder={""} />
          </Space>
          <Space size={4}>
            <Typography.Text>:</Typography.Text>
            <InputNumber value={hour} onChange={setHour} placeholder={""} />
          </Space>
          <Space size={4}>
            <Typography.Text>:</Typography.Text>
            <InputNumber
              value={minute}
              onChange={setMinute}
              placeholder={""}
            />
          </Space>
        </Space>
      </Col>

      <Col span={24}>
        <Space>
          <Button onClick={onCalc}></Button>
          <Typography.Text style={{ minWidth: "170px", display: "block" }}>
            : {calcValue}
          </Typography.Text>
          <Button onClick={onReset}></Button>
        </Space>
      </Col>
    </Row>
  );
};
